tinymce.Resource.add('tinymce.html-i18n.help-keynav.hr',
'<h1>Poetak navigacije na tipkovnici</h1>\n' +
  '\n' +
  '<dl>\n' +
  '  <dt>Fokusiranje trake izbornika</dt>\n' +
  '  <dd>Windows ili Linux: Alt+F9</dd>\n' +
  '  <dd>macOS: &#x2325;F9</dd>\n' +
  '  <dt>Fokusiranje alatne trake</dt>\n' +
  '  <dd>Windows ili Linux: Alt+F10</dd>\n' +
  '  <dd>macOS: &#x2325;F10</dd>\n' +
  '  <dt>Fokusiranje podnoja</dt>\n' +
  '  <dd>Windows ili Linux: Alt+F11</dd>\n' +
  '  <dd>macOS: &#x2325;F11</dd>\n' +
  '  <dt>Fokusiranje obavijesti</dt>\n' +
  '  <dd>Windows ili Linux: Alt + F12</dd>\n' +
  '  <dd>macOS: &#x2325;F12</dd>\n' +
  '  <dt>Fokusiranje kontekstne alatne trake</dt>\n' +
  '  <dd>Windows, Linux ili macOS: Ctrl+F9</dd>\n' +
  '</dl>\n' +
  '\n' +
  '<p>Navigacija e zapoeti kod prve stavke na korisnikom suelju, koja e biti istaknuta ili podcrtana ako se radi o prvoj stavci u\n' +
  '  putu elementa u podnoju.</p>\n' +
  '\n' +
  '<h1>Navigacija izmeu dijelova korisnikog suelja</h1>\n' +
  '\n' +
  '<p>Za pomicanje s jednog dijela korisnikog suelja na drugi pritisnite <strong>tabulator</strong>.</p>\n' +
  '\n' +
  '<p>Za pomicanje s jednog dijela korisnikog suelja na prethodni pritisnite <strong>Shift + tabulator</strong>.</p>\n' +
  '\n' +
  '<p>Ovo je redoslijed pomicanja <strong>tabulatora</strong> po dijelovima korisnikog suelja:</p>\n' +
  '\n' +
  '<ol>\n' +
  '  <li>Traka izbornika</li>\n' +
  '  <li>Pojedinane grupe na alatnoj traci</li>\n' +
  '  <li>Bona traka</li>\n' +
  '  <li>Put elemenata u podnoju</li>\n' +
  '  <li>Gumb za pomicanje po broju rijei u podnoju</li>\n' +
  '  <li>Veza na brand u podnoju</li>\n' +
  '  <li>Znaajka za promjenu veliine alata za ureivanje u podnoju</li>\n' +
  '</ol>\n' +
  '\n' +
  '<p>Ako neki dio korisnikog suelja nije naveden, on se preskae.</p>\n' +
  '\n' +
  '<p>Ako u podnoju postoji fokus za navigaciju na tipkovnici, a nema vidljive bone trake, pritiskom na <strong>Shift + tabulator</strong>\n' +
  '  fokus se prebacuje na prvu skupinu na alatnoj traci, ne na zadnju.</p>\n' +
  '\n' +
  '<h1>Navigacija unutar dijelova korisnikog suelja</h1>\n' +
  '\n' +
  '<p>Za pomicanje s jednog elementa korisnikog suelja na drugi pritisnite tipku s odgovarajuom <strong>strelicom</strong>.</p>\n' +
  '\n' +
  '<p>Tipke s <strong>lijevom</strong> i <strong>desnom</strong> strelicom</p>\n' +
  '\n' +
  '<ul>\n' +
  '  <li>slue za pomicanje izmeu izbornika na alatnoj traci.</li>\n' +
  '  <li>otvaraju podizbornik unutar izbornika.</li>\n' +
  '  <li>slue za pomicanje izmeu gumba unutar skupina na alatnoj traci.</li>\n' +
  '  <li>slue za pomicanje izmeu stavki na elementu puta u podnoju.</li>\n' +
  '</ul>\n' +
  '\n' +
  '<p>Tipke s <strong>donjom</strong> i <strong>gornjom</strong> strelicom</p>\n' +
  '\n' +
  '<ul>\n' +
  '  <li>slue za pomicanje izmeu stavki unutar izbornika.</li>\n' +
  '  <li>slue za pomicanje izmeu stavki na alatnoj traci skonog izbornika.</li>\n' +
  '</ul>\n' +
  '\n' +
  '<p>Tipkama <strong>strelica</strong> kruno se pomiete unutar dijela korisnikog suelja koji je u fokusu.</p>\n' +
  '\n' +
  '<p>Za zatvaranje otvorenog izbornika, otvorenog podizbornika ili otvorenog skonog izbornika pritisnite tipku <strong>Esc</strong>.</p>\n' +
  '\n' +
  '<p>Ako je fokus trenutano postavljen na vrh pojedinanog dijela korisnikog suelja, pritiskom na tipku <strong>Esc</strong> takoer\n' +
  '  u potpunosti zatvarate navigaciju na tipkovnici.</p>\n' +
  '\n' +
  '<h1>Izvravanje radnji putem stavki izbornika ili gumba na alatnoj traci</h1>\n' +
  '\n' +
  '<p>Nakon to se istakne stavka izbornika ili gumb na alatnoj traci s radnjom koju elite izvriti, pritisnite tipku <strong>Return</strong>, <strong>Enter</strong>\n' +
  '  ili <strong>razmak</strong> da biste pokrenuli eljenu radnju.</p>\n' +
  '\n' +
  '<h1>Navigacija dijalokim okvirima izvan kartica</h1>\n' +
  '\n' +
  '<p>Prilikom otvaranja dijalokih okvira izvan kartica fokus se nalazi na prvoj interaktivnoj komponenti.</p>\n' +
  '\n' +
  '<p>Navigaciju izmeu interaktivnih dijalokih komponenata vrite pritiskom na <strong>tabulator</strong> ili <strong>Shift + tabulator</strong>.</p>\n' +
  '\n' +
  '<h1>Navigacija dijalokim okvirima u karticama</h1>\n' +
  '\n' +
  '<p>Prilikom otvaranja dijalokih okvira u karticama fokus se nalazi na prvom gumbu u izborniku unutar kartice.</p>\n' +
  '\n' +
  '<p>Navigaciju izmeu interaktivnih komponenata dijalokog okvira u kartici vrite pritiskom na <strong>tabulator</strong> ili\n' +
  '  <strong>Shift + tabulator</strong>.</p>\n' +
  '\n' +
  '<p>Na karticu s drugim dijalokim okvirom moete se prebaciti tako da stavite fokus na izbornik kartice pa pritisnete tipku s odgovarajuom <strong>strelicom</strong>\n' +
  '  za kruno pomicanje izmeu dostupnih kartica.</p>\n');